



















































































import Vue from 'vue';
import { Ripple } from 'vuetify/lib/directives/';
import Oa from '../models/Oa';
import Ie from '../models/Ie';
import Dua from '../models/Dua';

export default Vue.extend({
    name: 'RomaMessageModal',
    props: {
        value: Boolean,
        title: {
            type: String,
            default: 'Comentarios'
        },
        maxWidth: {
            type: String,
            default: '80%',
        },
        overlayOpacity: {
            type: String,
            default: '0.8',
        },
        persistent: {
            type: Boolean,
            default: true,
        },
        messages: {
            type: Array,
            default () {
                return [];
            }
        },
        placeholder: {
            type: String,
            default: 'Agrega un comentario...'
        }
    },
    data() {
        return {
            valid: false,
            loading: false,
            showing: this.value,
            showSuggestions: false,
            message: '',
            comments: this.messages,
            sugges: '',
            suggestions: [],
            suggestionsX: null,
            suggestionsY: null,
        };
    },
    computed: {

    },
    methods: {
        show(value: Boolean) {
            this.$emit('input', value);
        },
        comment (event) {
            this.message = this.message.slice(0 , this.message.length - 1);
            if (this.message !== '\n' && this.message !== '') {
                const data = {user: {name: 'Lorem ipsum', avatar: 'http://i.pravatar.cc/64'}, message: this.message.trim(), date: new Date().toDateString()};
                this.comments.push(data);
                this.message = '';
            }

            event.preventDefault();

            return false;
        },
        suggestion (event) {
            this.showSuggestions = false;

            if (this.sugges[0] === '@' && event.key !== '@') {
                if (event.code === 'Space') {
                    this.suggestionsX = event.target.selectionEnd + 120;
                    this.suggestionsY = event.target.offsetHeight + 170;
                    if (this.sugges.indexOf('oa') > 0) {
                      this.suggestions = Oa.all();
                      this.showSuggestions = true;
                    } else if (this.sugges.indexOf('ie') > 0) {
                      this.suggestions = Ie.all();
                      this.showSuggestions = true;
                    } else if (this.sugges.indexOf('dua') > 0) {
                      this.suggestions = Dua.all();
                      this.showSuggestions = true;
                    } else {
                        this.suggestions = [];
                    }

                    this.sugges = '';
                }
                if (event.code !== 'Backspace' && event.code !== 'Space' && event.key !== 'Control' && event.key !== 'Shift') {
                    this.sugges += event.key;
                }
            }
            if (event.key === '@') {
                this.sugges += event.key;
            }
        },
        insertSuggestion (item) {
            console.log(this.$refs.textarea);
            const index = this.message.lastIndexOf('@');
            this.sugges = '';
            this.$refs.textarea.$refs.input.focus();
            this.message = this.message.slice(0, index) + '@' + item.cod;
            // this.$refs.textarea.$refs.input.setSelectionRange(index, this.message.length);
        }
    },
    directives: {
        Ripple
    }
});
